<script>
import {
  mdbBtn,
  mdbCard,
  mdbCardText,
  mdbIcon,
  mdbBreadcrumb,
  mdbBreadcrumbItem,
  mdbView,
  mdbMask,
} from "mdbvue";
import ItemCard from "../components/ItemCard";

export default {
  components: {
    mdbBtn,
    ItemCard,
    mdbCard,
    mdbCardText,
    mdbIcon,
    mdbBreadcrumb,
    mdbBreadcrumbItem,
    mdbView,
    mdbMask,
  },
  data() {
    return {
      fourRandom: [],
      id: this.$route.params.id,
      product: {},
      allImages: [
        "1.jpg",
        "2.jpg",
        "3.jpg",
        "4.jpg",
        "5.jpg",
        "6.jpg",
        "7.jpg",
        "8.jpg",
        "9.jpg",
        "10.jpg",
        "11.jpg",
        "12.jpg",
        "13.jpg",
        "16.jpg",
      ],
      images: [],
    };
  },
  props: {
    items: Array,
  },
  mounted() {
    this.getProduct();
    this.randomImages();
    this.randomProducts();
  },
  watch: {
    $route(to) {
      this.id = to.params.id;
      this.getProduct();
      this.randomImages();
      this.randomProducts();
    },
  },
  methods: {
    getProduct() {
      this.items.forEach((item) => {
        if (item.id == this.id) {
          this.product = item;
        }
      });
    },
    randomImages() {
      let temp = [...this.allImages];
      let images = [];
      for (let i = 0; i < 6; i++) {
        let n = Math.floor(Math.random() * temp.length);
        images.push(temp[n]);
        temp.splice(n, 1);
      }
      this.images = images;
    },
    randomProducts() {
      let temp = [...this.items];
      let products = [];
      for (let i = 0; i < 4; i++) {
        let n = Math.floor(Math.random() * temp.length);
        products.push(temp[n]);
        temp.slice(n, 1);
      }
      this.fourRandom = products;
    },
  },
};
</script>

